import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ConsultasService } from '../../../../../core/consultaService/consultas.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { DatePipe } from '@angular/common';
import { AuthService } from '../../../../../core/consultaService/auth.service';
import { AdministracionOperativaService } from '../../../../../core/consultaService/administracionOperativa.service';

const swal = require('sweetalert');
declare var jQuery: any;
declare var $: any;

@Component({
  selector: 'app-navUsuarios',
  templateUrl: './navUsuarios.component.html',
  styleUrls: ['./navUsuarios.component.scss']
})
export class NavUsuariosComponent implements OnInit {

  forma: FormGroup;
  buscaUsuarios: any = {};
  request: any = {};
  deletePerfil: any = {};
  savePerfil: any = {};
  user: any = {};
  pActualPerfilesSeleccionados = 1;
  perfilesSeleccionados: Array <any> = [];
  perfilesDisponibles: Array <any> = [];

  constructor(public http: HttpClient, fb: FormBuilder, private modalService: BsModalService,
    private setearDatos: ConsultasService, private authService: AuthService,
    public loader: NgxSpinnerService, private router: Router,
    private datePipe: DatePipe, private usuariosService: AdministracionOperativaService) {
      this.forma = new FormGroup ({
        nRunUsuario: new FormControl(''),
        sCodPerfil: new FormControl(''),
      });
    }

  ngOnInit() {
    this.user = this.authService.getDatosUser();
    this.listarPerfilesUsuario();
  }
  listarPerfilesUsuario() {
    this.request.nRunUsuario = Number(this.usuariosService.selectedUsers.usu_nrunusuario);
    this.usuariosService.getPerfilesUsuario(this.request)
        .subscribe((data: any) => {
            (this.perfilesSeleccionados = data.perfilesActuales);
            (this.perfilesDisponibles = data.perfilesDisponibles);
            this.forma.controls.sCodPerfil.setValue(this.perfilesDisponibles[0].par_codparametro02);
        });
  }
  agregarPerfil () {
    this.loader.show();
    this.forma.controls.nRunUsuario.setValue(Number(this.usuariosService.selectedUsers.usu_nrunusuario));
    this.savePerfil = this.forma.value;
    this.usuariosService.savePerfilesUsuario(this.savePerfil)
        .subscribe((data: any) => {
          if (data.error.codError === 0) {
            this.loader.hide();
            swal('Bien', 'El Perfil se ha agregado correctamente', 'success' );
            this.forma.controls.sCodPerfil.setValue('');
            this.listarPerfilesUsuario();
          } else {
            swal('Atención', data.error.msjError, 'error');
            this.loader.hide();
          }
        },
        error => {
          this.loader.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
  }
  eliminar(perfileSeleccionado) {
    this.loader.show();
    this.deletePerfil.nRunUsuario = Number(perfileSeleccionado.per_nrunusuario);
    this.deletePerfil.sCodPerfil = perfileSeleccionado.per_scodperfil;
    this.usuariosService.deletePerfilesUsuario(this.deletePerfil)
        .subscribe((data: any) => {
          if (data.error.codError === 0) {
            this.loader.hide();
            swal('Bien', 'El Perfil se ha eliminado correctamente', 'success' );
            this.listarPerfilesUsuario();
          } else {
            swal('Atención', data.error.msjError, 'error');
            this.loader.hide();
          }
        },
        error => {
          this.loader.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
  }

}
