import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ConsultasService } from '../../../../../core/consultaService/consultas.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { DatePipe } from '@angular/common';
import { AuthService } from '../../../../../core/consultaService/auth.service';
import { AdministracionOperativaService } from '../../../../../core/consultaService/administracionOperativa.service';

const swal = require('sweetalert');
declare var jQuery: any;
declare var $: any;

@Component({
  selector: 'app-navSucursales',
  templateUrl: './navSucursales.component.html',
  styleUrls: ['./navSucursales.component.scss']
})
export class NavSucursalesComponent implements OnInit {

  forma: FormGroup;
  buscaUsuarios: any = {};
  listarUsuarios: any = {};
  deleteUsuario: any = {};
  saveUsuarioSucursal: any = {};
  user: any = {};
  usuariosSucursal: Array <any> = [];
  usuariosSeleccionadosSucursal: Array <any> = [];
  pActualSucursales = 1;

  constructor(public http: HttpClient, fb: FormBuilder, private modalService: BsModalService,
    private setearDatos: ConsultasService, private authService: AuthService,
    public loader: NgxSpinnerService, private router: Router,
    private datePipe: DatePipe, private sucursalService: AdministracionOperativaService) {
      this.forma = new FormGroup ({
        nRutOperador: new FormControl('', Validators.required),
        nRutUsuario: new FormControl('', Validators.required),
        sCodSucursal: new FormControl('', Validators.required),
      });
     }

  ngOnInit() {
  this.user = this.authService.getDatosUser();
  this.usuariosDisponibles();
  this.listarUsuariosSeleccionados();
  }
  usuariosDisponibles() {
    this.buscaUsuarios.nRutOperador = Number(this.user.nrutoperador);
    this.buscaUsuarios.sCodSucursal = this.sucursalService.selectedSucursal.suc_scodsucursal;
    this.sucursalService.getUsuariosSucursales(this.buscaUsuarios)
        .subscribe((data: any) => {
            (this.usuariosSucursal = data.usuarioSucursal);
            this.forma.controls.nRutUsuario.setValue(this.usuariosSucursal[0].usu_nrunusuario);
        });
  }
  listarUsuariosSeleccionados() {
    this.listarUsuarios.nRutOperador = Number(this.user.nrutoperador);
    this.listarUsuarios.sCodSucursal = this.sucursalService.selectedSucursal.suc_scodsucursal;
    this.sucursalService.getUsuariosSeleccionadosSucursales(this.listarUsuarios)
        .subscribe((data: any) => {
            (this.usuariosSeleccionadosSucursal = data.usuarioSucursal);
        });
  }
  agregarUsuario () {
    this.forma.controls.sCodSucursal.setValue(this.sucursalService.selectedSucursal.suc_scodsucursal);
    this.forma.controls.nRutOperador.setValue(Number(this.user.nrutoperador));
    this.saveUsuarioSucursal = this.forma.value;
    this.loader.show();
    this.sucursalService.saveUsuariosSeleccionadosSucursales(this.saveUsuarioSucursal)
        .subscribe((data: any) => {
          if (data.error.codError === 0) {
            this.loader.hide();
            swal('Bien', 'El Usuario se ha agregado correctamente', 'success' );
            this.listarUsuariosSeleccionados();
            this.usuariosDisponibles();
          } else {
            swal('Atención', data.error.msjError, 'error');
            this.loader.hide();
          }
        },
        error => {
          this.loader.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
  }
  eliminar(usuarioSeleccionado) {
    this.loader.show();
    this.deleteUsuario.nRunUsuario = usuarioSeleccionado.usu_nrunusuario;
    this.deleteUsuario.nRutOperador = Number(this.user.nrutoperador);
    this.deleteUsuario.sCodSucursal = this.sucursalService.selectedSucursal.suc_scodsucursal;
    this.sucursalService.deleteUsuariosSeleccionadosSucursales(this.deleteUsuario)
        .subscribe((data: any) => {
          if (data.error.codError === 0) {
            this.loader.hide();
            swal('Bien', 'El usuario se ha eliminado correctamente', 'success' );
            this.listarUsuariosSeleccionados();
            this.usuariosDisponibles();
          } else {
            swal('Atención', data.error.msjError, 'error');
            this.loader.hide();
          }
        },
        error => {
          this.loader.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
  }
}
