import { Component, OnInit } from '@angular/core';
import * as _ from 'lodash';
import { HttpClient } from '@angular/common/http';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';

import { NgxSpinnerService } from 'ngx-spinner';
import { AuthService } from '../../../core/consultaService/auth.service';
import { Router } from '@angular/router';
import { AdministracionOperativaService } from '../../../core/consultaService/administracionOperativa.service';

declare var jQuery: any;
declare var $: any;

const swal = require('sweetalert');

@Component({
  selector: 'app-recorrido',
  templateUrl: './recorrido.component.html',
  styleUrls: ['./recorrido.component.scss']
})
export class RecorridoComponent implements OnInit {
  forma: FormGroup;
  daiasRecorridos: Array<any> = [
    {
      codDia: null,
      descEstado: '(Todos)'
    },
    {
      codDia: 'LUNES',
      descEstado: 'Lunes'
    },
    {
      codDia: 'MARTE',
      descEstado: 'Martes'
    },
    {
      codDia: 'MIERC',
      descEstado: 'Miercoles'
    },
    {
      codDia: 'JUEVE',
      descEstado: 'Jueves'
    },
    {
      codDia: 'VIERN',
      descEstado: 'Viernes'
    },
    {
      codDia: 'SABAD',
      descEstado: 'Sabado'
    },
    {
      codDia: 'DOMIN',
      descEstado: 'Domingo'
    }
  ];
  datos: Array<any> = [];
  user: any = {};
  request: any = {};
  requestdos: any = {};
  recorridos: Array <any> = [];
  pActualBuscaRecorridos = 1;
  detalleRecorrido: any = {};

  constructor(public http: HttpClient, fb: FormBuilder,
    private recorridosService: AdministracionOperativaService,
    public loader: NgxSpinnerService, private authService: AuthService, private router: Router) {
      this.forma = new FormGroup ({
        codRecorrido: new FormControl(''),
        descripcion: new FormControl('' ),
        diasRecorridos: new FormControl('')
    });
  }

  ngOnInit(): void {
    this.user = this.authService.getDatosUser();
    if (this.recorridosService.busquedaRecorrido !== undefined) {
      this.buscar(true);
    }
  }
  buscar (conBusqueda?) {
    this.loader.show();
    if (conBusqueda) {
        this.request = this.recorridosService.busquedaRecorrido;
        this.forma = this.recorridosService.formaRecorrido;
      } else {
        this.request.nRutOperador = this.user.nrutoperador;
        this.request.sCodRecorrido = this.forma.controls.codRecorrido.value;
        this.request.sDesRecorrido = this.forma.controls.descripcion.value;
        this.request.sDia = this.forma.controls.diasRecorridos.value;
        this.recorridosService.busquedaRecorrido = Object.assign({}, this.request);
        this.recorridosService.formaRecorrido = this.forma;
       }
    this.recorridosService.getRecorridos(this.request)
        .subscribe((data: any) => {
          this.loader.hide();
          if (data.recorridosAdministracion === null || data.error.codError !== 0) {
            swal('Atención', data.error.msjError, 'error');
          } else {
            if (data.recorridosAdministracion.length === 0 && data.error.codError === 0 ) {
                swal('Atención', 'No se encontraron resultados', 'info');
            } else {
                (this.recorridos = data.recorridosAdministracion);
            }
          }
        },
        error => {
          this.loader.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
  }
  editarRecorrido(recorrido): void {
    this.loader.show();
    this.requestdos.nRutOperador = recorrido.dre_nrutoperador;
    this.requestdos.sCodRecorrido = recorrido.dre_scodrecorrido;
    this.recorridosService.getDetallesRecorridos(this.requestdos)
        .subscribe((data: any) => {
            if (data.definicionRecorrido != null) {
                (this.detalleRecorrido = data.definicionRecorrido);
                this.recorridosService.selectedRecorrido = Object.assign({}, this.detalleRecorrido[0]);
            } else {
                swal('Atención', data.error.msjError, 'error');
                this.loader.hide();
            }
            if (this.recorridosService.selectedRecorrido !== undefined ) {
                this.router.navigate(['/administracionOperativa/detalleRecorridos']);
            } else {
                this.loader.hide();
            }
            this.loader.hide();
        },
        error => {
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            this.loader.hide();
        });
  }

  crearRecorrido() {
    this.recorridosService.selectedRecorrido = undefined;
    this.router.navigate(['/administracionOperativa/detalleRecorridos']);
  }
}
