import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { NgxSpinnerService } from 'ngx-spinner';

import { AuthService } from '../../../core/consultaService/auth.service';
import { AdministracionOperativaService } from '../../../core/consultaService/administracionOperativa.service';
import { SucursalesPopUpComponent } from '../../commonPopUp/sucursalesPopUp/sucursalesPopUp.component';


const swal = require('sweetalert');
declare var jQuery: any;
declare var $: any;


@Component({
  selector: 'app-bodega',
  templateUrl: './bodega.component.html',
  styleUrls: ['./bodega.component.scss']
})
export class BodegaComponent implements OnInit, OnDestroy {
  forma: FormGroup;
  sucursalModal: BsModalRef;
  origen: string;
  titulo: string;
  pActualBuscaBodega = 1;
  user: any = {};
  bodegas: Array <any> = [];
  subData: any;
  request: any = {};
  requestdos: any = {};
  detalleRutas: any = {};
  datos: Array <any> = [];

  constructor(public http: HttpClient, fb: FormBuilder, private modalService: BsModalService,
    private authService: AuthService, public loader: NgxSpinnerService, private router: Router,
    private bodegasService: AdministracionOperativaService) {
      this.forma = new FormGroup ({
        sCodBodega: new FormControl(''),
        sCodEstado: new FormControl(''),
        sCodSucursal: new FormControl(''),
        sDescSucursal: new FormControl({value: '', disabled: true }),
        sDescBodega: new FormControl(''),
        runOperador: new FormControl ('')
    });
     }

  ngOnInit() {
  this.user = this.authService.getDatosUser();
  if (this.bodegasService.busquedaBodega !== undefined) {
    this.buscar(true);
  }
  this.subData = this.bodegasService.datos$.subscribe(resData => {
      this.setData(resData);
    });
  }
  ngOnDestroy() {
    if (this.subData !== undefined) {
      this.subData.unsubscribe();
    }
  }
  openModalSucursales() {
    this.origen = 'sucursales';
    const initialState = {
      titulo: 'Sucursales',
      origen: 'sucursales'
    };
    this.sucursalModal = this.modalService.show(SucursalesPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }
  validaSucursales() {
    this.loader.show();
    if (this.forma.controls.sCodSucursal.value === '') {
      this.loader.hide();
      this.forma.controls.sCodSucursal.setValue('');
      this.forma.controls.sDescSucursal.setValue('');
    } else {
      this.requestdos.nRutOperador = Number(this.user.nrutoperador);
      this.requestdos.sCodSucursal = this.forma.controls.sCodSucursal.value.toUpperCase();
      this.bodegasService.getCargaSucursales(this.requestdos)
            .subscribe((data: any) => {
              this.loader.hide();
              if (data) {
                if (data.error.codError !== 0) {
                    swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                  } else {
                    this.datos = data.sucursal;
                    if (this.datos.length > 0) {
                      this.forma.controls.sDescSucursal.setValue(this.datos[0].suc_sdescsucursal);
                    } else {
                      this.forma.controls.sCodSucursal.setValue('');
                      this.forma.controls.sDescSucursal.setValue('');
                      swal('Atención', 'No Existe esta Sucursal', 'warning');
                    }
                  }
              }
            },
            error => {
              this.loader.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            });
    }
  }
  setData(resData) {
    if (resData !== 0) {
      switch (this.origen) {
        case 'sucursales': {
          this.forma.controls.sCodSucursal.setValue(resData.suc_scodsucursal);
          this.forma.controls.sDescSucursal.setValue(resData.suc_sdescsucursal);
          break;
        }
      }
    }
    $('body').removeClass('modal-open');
  }
  buscar (conBusqueda?) {
    this.loader.show();
    if (conBusqueda) {
        this.request = this.bodegasService.busquedaBodega;
        this.forma = this.bodegasService.formaBodega;
      } else {
        this.forma.controls.runOperador.setValue(Number(this.user.nrutoperador));
        this.request.runOperador = Number(this.forma.controls.runOperador.value);
        this.request.sCodBodega = this.forma.controls.sCodBodega.value.toUpperCase();
        this.request.sCodEstado = this.forma.controls.sCodEstado.value;
        this.request.sCodSucursal = this.forma.controls.sCodSucursal.value;
        this.request.sDescBodega = this.forma.controls.sDescBodega.value;
        this.bodegasService.busquedaBodega = Object.assign({}, this.request);
        this.bodegasService.formaBodega = this.forma;
      }
    
    this.bodegasService.getBodegas(this.request)
        .subscribe((data: any) => {
          this.loader.hide();
          if (data) {
            if (data.bodegas === null || data.error.codError !== 0) {
              swal('Atención', data.error.msjError, 'error');
            } else {
              if (data.bodegas.length === 0 && data.error.codError === 0 ) {
                  swal('Atención', 'No se encontraron resultados', 'info');
              } else {
                (this.bodegas = data.bodegas);
              }
            }
          } else {
            swal('Atención', data.error.msjError, 'error');
          }
        },
        error => {
            this.loader.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        }
    );
  }
  editarBodega(bodega): void {
    this.loader.show();
    this.bodegasService.selectedBodega = Object.assign({}, bodega);
    if (this.bodegasService.selectedBodega !== undefined ) {
        this.router.navigate(['/administracionOperativa/detalleBodegas']);
        this.loader.hide();
    } else {
      swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      this.loader.hide();
    }
  }
  crearBodegas() {
    this.bodegasService.selectedBodega = undefined;
    this.router.navigate(['/administracionOperativa/detalleBodegas']);
  }

}
