import { Component, OnInit, OnDestroy } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import * as _ from 'lodash';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { NgxSpinnerService } from 'ngx-spinner';
import { AdministracionOperativaService } from '../../../../../../core/consultaService/administracionOperativa.service';
import { ChoferPopUpComponent } from '../../../../../commonPopUp/choferPopUp/choferPopUp.component';

declare var jQuery: any;
declare var $: any;

@Component({
  selector: 'app-navConductores',
  templateUrl: './navConductores.component.html',
  styleUrls: ['./navConductores.component.scss']
})
export class NavConductoresComponent implements OnInit, OnDestroy {
  request: any = {};
  conductores: Array <any> = [];
  ruttransporte = this.transportistaService.selectedTransportista.tra_nruttransportista;
  rutoperador = this.transportistaService.selectedTransportista.tra_nrutoperador;
  choferesModal: BsModalRef;
  origen: string;
  pActualconductor = 1;
  reloadTabla: any;

  constructor(public http: HttpClient, private transportistaService: AdministracionOperativaService,
    private modalService: BsModalService, public loader: NgxSpinnerService) { }

  ngOnInit() {
    this.reloadTabla = this.transportistaService.datos$.subscribe(resData => {
      this.setTipo(resData);
    });
    this.listadoConductores();
  }
  ngOnDestroy() {
    if (this.reloadTabla !== undefined) {
      this.reloadTabla.unsubscribe();
    }
  }
  listadoConductores() {
    this.request.rutOperador = this.rutoperador;
    this.request.rutTransportista = this.ruttransporte;
    this.transportistaService.getConductores(this.request)
        .subscribe((data: any) => {
            (this.conductores = data.conductores);
        });

  }
  editarConductor(conductor) {
    this.origen = 'conductor';
    const initialState = {
      titulo: 'Detalle Conductor',
      conductor: conductor
    };
    this.loader.show();
      this.choferesModal = this.modalService.show(ChoferPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
      this.loader.hide();
  }
  openModalConductor () {
    this.origen = 'conductor';
    const initialState = {
      titulo: 'Detalle Conductor',
      origen: 'conductor',
    };
    this.choferesModal = this.modalService.show(ChoferPopUpComponent, {initialState, backdrop: 'static', keyboard: false });
  }
  setTipo(resData) {
    if (resData !== 0) {
      switch (this.origen) {
        case 'conductor': {
          if (resData = 'succesChofer') {
            this.listadoConductores();
          }
          break;
        }
      }
    }
    $('body').removeClass('modal-open');
  }

}
