import { Component, OnInit, OnDestroy } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import * as _ from 'lodash';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { NgxSpinnerService } from 'ngx-spinner';
import { AdministracionOperativaService } from '../../../../../../core/consultaService/administracionOperativa.service';
import { VehiculosPopUpComponent } from '../../../../../commonPopUp/vehiculosPopUp/vehiculosPopUp.component';

declare var jQuery: any;
declare var $: any;

@Component({
  selector: 'app-navVehiculo',
  templateUrl: './navVehiculo.component.html',
  styleUrls: ['./navVehiculo.component.scss']
})
export class NavVehiculoComponent implements OnInit, OnDestroy {
  vehiculosTransportista: any = {};
  vehiculos: Array <any> = [];
  ruttransporte = this.transportistaService.selectedTransportista.tra_nruttransportista;
  rutoperador = this.transportistaService.selectedTransportista.tra_nrutoperador;
  veiculosModal: BsModalRef;
  origen: string;
  pActualvehiculo = 1;
  reloadTabla: any;

  constructor(public http: HttpClient, private transportistaService: AdministracionOperativaService,
    private modalService: BsModalService, public loader: NgxSpinnerService) { }

  ngOnInit() {
    this.reloadTabla = this.transportistaService.datos$.subscribe(resData => {
      this.setTipo(resData);
    });
    this.listadoVehiculos();
  }
  ngOnDestroy() {
    if (this.reloadTabla !== undefined) {
      this.reloadTabla.unsubscribe();
    }
  }
  listadoVehiculos() {
    this.vehiculosTransportista.rutOperador = this.rutoperador;
    this.vehiculosTransportista.rutTransportista = this.ruttransporte;
    this.transportistaService.getVehiculos(this.vehiculosTransportista)
        .subscribe((data: any) => {
          console.log(data);
            (this.vehiculos = data.vehiculos);
        });
  }
  editarVehiculo(vehiculo) {
    this.origen = 'vehiculos';
    const initialState = {
      titulo: 'Detalle Vehículos',
      vehiculo: vehiculo
    };
    this.loader.show();
      this.veiculosModal = this.modalService.show(VehiculosPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
      this.loader.hide();
  }
  openModalVehiculo () {
    this.origen = 'vehiculos';
    const initialState = {
      titulo: 'Detalle Vehículos',
      origen: 'vehiculos',
    };
    this.veiculosModal = this.modalService.show(VehiculosPopUpComponent, {initialState, backdrop: 'static', keyboard: false });
  }
  setTipo(resData) {
    if (resData !== 0) {
      switch (this.origen) {
        case 'vehiculos': {
          if (resData = 'succesLocales') {
            this.listadoVehiculos();
          }
          break;
        }
      }
    }
    $('body').removeClass('modal-open');
  }

}
