import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { NgxSpinnerService } from 'ngx-spinner';

import { AuthService } from '../../../core/consultaService/auth.service';
import { AdministracionOperativaService } from '../../../core/consultaService/administracionOperativa.service';
import { RecorridosGeneralPopUpComponent } from '../../commonPopUp/recorridosGeneralPopUp/recorridosGeneralPopUp.component';

const swal = require('sweetalert');
declare var jQuery: any;
declare var $: any;


@Component({
  selector: 'app-rutas',
  templateUrl: './rutas.component.html',
  styleUrls: ['./rutas.component.scss']
})
export class RutasComponent implements OnInit, OnDestroy {

  forma: FormGroup;
  recorridosRutaModal: BsModalRef;
  origen: string;
  titulo: string;
  pActualBuscaRutas = 1;
  user: any = {};
  rutas: Array <any> = [];
  subData: any;
  request: any = {};
  requestdos: any = {};
  detalleRutas: any = {};
  datos: Array <any> = [];

  constructor(public http: HttpClient, fb: FormBuilder, private modalService: BsModalService,
    private authService: AuthService, public loader: NgxSpinnerService, private router: Router,
    private rutasService: AdministracionOperativaService) {
    this.forma = new FormGroup ({
        sCodRuta: new FormControl(''),
        sDescripcion: new FormControl(''),
        sCodRecorrido: new FormControl(''),
        recorrido: new FormControl({value: '', disabled: true }),
        nRutOperador: new FormControl ()
    });
  }

  ngOnInit() {
    this.user = this.authService.getDatosUser();
    if (this.rutasService.busquedaRutas !== undefined) {
      this.buscar(true);
    }
    this.subData = this.rutasService.datos$.subscribe(resData => {
        this.setData(resData);
      });
  }
  ngOnDestroy() {
    if (this.subData !== undefined) {
      this.subData.unsubscribe();
    }
  }
  openModalRecorridos() {
    this.origen = 'recorridosRutas';
    const initialState = {
      titulo: 'Recorridos',
      origen: 'recorridosRutas'
    };
    this.recorridosRutaModal = this.modalService.show(RecorridosGeneralPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }
  validaRecorridos() {
    this.loader.show();
    if (this.forma.controls.sCodRecorrido.value === '') {
      this.loader.hide();
      this.forma.controls.sCodRecorrido.setValue('');
      this.forma.controls.recorrido.setValue('');
    } else {
      this.requestdos.nRutOperador = Number(this.user.nrutoperador);
      this.requestdos.sCodRecorrido = this.forma.controls.sCodRecorrido.value;
      this.rutasService.getDetallesRecorridos(this.requestdos)
            .subscribe((data: any) => {
              this.loader.hide();
              if (data) {
                if (data.error.codError !== 0) {
                    swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
                  } else {
                    this.datos = data.definicionRecorrido;
                    if (this.datos.length > 0) {
                      this.forma.controls.recorrido.setValue(this.datos[0].dre_sdesrecorrido);
                    } else {
                      this.forma.controls.sCodRecorrido.setValue('');
                      this.forma.controls.recorrido.setValue('');
                      swal('Atención', 'No Existe este Recorrido', 'warning');
                    }
                  }
              }
            },
            error => {
              this.loader.hide();
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            });
    }
  }
  setData(resData) {
    if (resData !== 0) {
      switch (this.origen) {
        case 'recorridosRutas': {
          this.forma.controls.sCodRecorrido.setValue(resData.dre_scodrecorrido);
          this.forma.controls.recorrido.setValue(resData.dre_sdesrecorrido);
          break;
        }
      }
    }
    $('body').removeClass('modal-open');
  }
  buscar (conBusqueda?) {
    this.loader.show();
    if (conBusqueda) {
      this.request = this.rutasService.busquedaRutas;
      this.forma = this.rutasService.formaRutas;
    } else {
      this.forma.controls.nRutOperador.setValue(Number(this.user.nrutoperador));
      this.request = this.forma.value;
      this.rutasService.busquedaRutas = Object.assign({}, this.request);
      this.rutasService.formaRutas = this.forma;
    }
    this.rutasService.getRutas(this.request)
        .subscribe((data: any) => {
            this.loader.hide();
                if (data.rutasAdministracion.length === 0 && data.error.codError === 0 ) {
                    swal('Atención', 'No se encontraron resultados', 'info');
                } else {
                    if (data.rutasAdministracion === null) {
                      swal('Atención', data.error.msjError, 'error');
                    } else {
                        (this.rutas = data.rutasAdministracion);
                    }
                }
                if (data.error.codError !== 0) {
                  swal('Atención', data.error.msjError, 'error');
                }
        },
        error => {
            this.loader.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        }
    );
    }
   editarRuta(ruta): void {
      this.loader.show();
      this.requestdos.nRutOperador = ruta.rut_nrutoperador;
      this.requestdos.sCodRuta = ruta.rut_scodruta;
      this.rutasService.getDetallesRutas(this.requestdos)
          .subscribe((data: any) => {
              this.loader.hide();
              if (data.rutas != null) {
                  (this.detalleRutas = data.rutas);
                  this.rutasService.selectedRutas = Object.assign({}, this.detalleRutas[0]);
                  if (this.rutasService.selectedRutas !== undefined ) {
                    this.router.navigate(['/administracionOperativa/detalleRutas']);
                  } else {
                      this.loader.hide();
                  }
              } else {
                  swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
              }
          },
          error => {
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
              this.loader.hide();
          });
    }
  crearRutas() {
    this.rutasService.selectedRutas = undefined;
    this.router.navigate(['/administracionOperativa/detalleRutas']);
  }
}