import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { Ng2TableModule } from 'ng2-table/ng2-table';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { NgxPaginationModule } from 'ngx-pagination';
import { SharedModule } from '../../shared/shared.module';
import { CommonPopUpModule } from '../commonPopUp/commonPopUp.module';
import { CommonModule } from '@angular/common';

import { TransportistasComponent } from './transportistas/transportistas.component';
import { DetalleTransportistaComponent } from './transportistas/detalleTransportista/detalleTransportista.component';
import { NavTransportistaComponent } from './transportistas/detalleTransportista/navTransportista/navTransportista.component';
import { RutasComponent } from './rutas/rutas.component';
import { DetalleRutasComponent } from './rutas/detalleRutas/detalleRutas.component';
import { UsuariosComponent } from './usuarios/usuarios.component';
import { DetalleUsuariosComponent } from './usuarios/detalleUsuarios/detalleUsuarios.component';
import { NavUsuariosComponent } from './usuarios/detalleUsuarios/navUsuarios/navUsuarios.component';
import { BodegaComponent } from './bodega/bodega.component';
import { SucursalComponent } from './sucursal/sucursal.component';
import { DetalleBodegaComponent } from './bodega/detalleBodega/detalleBodega.component';
import { DetalleSucursalComponent } from './sucursal/detalleSucursal/detalleSucursal.component';
import { RecorridoComponent } from './recorrido/recorrido.component';
import { DetalleRecorridoComponent } from './recorrido/detalleRecorrido/detalleRecorrido.component';
import { UtilGeoPopUpComponent } from '../commonPopUp/utilGeoPopUp/utilGeoPopUp.component';
import { NavVehiculoComponent } from './transportistas/detalleTransportista/navTransportista/navVehiculo/navVehiculo.component';
import { NavConductoresComponent } from './transportistas/detalleTransportista/navTransportista/navConductores/navConductores.component';
import { ConductororesPopUpComponent } from '../commonPopUp/conductororesPopUp/conductororesPopUp.component';
import { VehiculosPopUpComponent } from '../commonPopUp/vehiculosPopUp/vehiculosPopUp.component';
import { ChoferPopUpComponent } from '../commonPopUp/choferPopUp/choferPopUp.component';
import { SucursalesPopUpComponent } from '../commonPopUp/sucursalesPopUp/sucursalesPopUp.component';
import { TransportistasPopUpComponent } from '../commonPopUp/transportistasPopUp/transportistasPopUp.component';
import { RecorridosGeneralPopUpComponent } from '../commonPopUp/recorridosGeneralPopUp/recorridosGeneralPopUp.component';
import { ZonasTodasPopUpComponent } from '../commonPopUp/zonasTodasPopUp/zonasTodasPopUp.component';
import { TarifasPopUpComponent } from '../commonPopUp/tarifasPopUp/tarifasPopUp.component';
import { NavSucursalesComponent } from './sucursal/detalleSucursal/navSucursales/navSucursales.component';
import { OperadoresPopUpComponent } from '../commonPopUp/operadoresPopUp/operadoresPopUp.component';


const routes: Routes = [
  { path: 'transportistas', component: TransportistasComponent},
  { path: 'detalleTransportista', component: DetalleTransportistaComponent},
  { path: 'rutas', component: RutasComponent },
  { path: 'detalleRutas', component: DetalleRutasComponent },
  { path: 'usuarios', component: UsuariosComponent },
  { path: 'detalleUsuarios', component: DetalleUsuariosComponent },
  { path: 'bodegas', component: BodegaComponent },
  { path: 'detalleBodegas', component: DetalleBodegaComponent },
  { path: 'sucursales', component: SucursalComponent },
  { path: 'detalleSucursales', component: DetalleSucursalComponent },
  { path: 'recorridos', component: RecorridoComponent },
  { path: 'detalleRecorridos', component: DetalleRecorridoComponent }
];

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    RouterModule.forChild(routes),
    Ng2TableModule,
    NgxDatatableModule,
    NgxPaginationModule,
    CommonPopUpModule
  ],
  declarations: [
    TransportistasComponent,
    DetalleTransportistaComponent,
    NavTransportistaComponent,
    RutasComponent,
    DetalleRutasComponent,
    UsuariosComponent,
    DetalleUsuariosComponent,
    NavUsuariosComponent,
    BodegaComponent,
    SucursalComponent,
    DetalleBodegaComponent,
    DetalleSucursalComponent,
    RecorridoComponent,
    DetalleRecorridoComponent,
    NavVehiculoComponent,
    NavConductoresComponent,
    NavSucursalesComponent,
  ],
  entryComponents: [ UtilGeoPopUpComponent,
    ConductororesPopUpComponent,
    VehiculosPopUpComponent,
    ChoferPopUpComponent,
    SucursalesPopUpComponent,
    TransportistasPopUpComponent,
    RecorridosGeneralPopUpComponent,
    ZonasTodasPopUpComponent,
    TarifasPopUpComponent,
    OperadoresPopUpComponent
    ],
  exports: [
    RouterModule
]
})
export class AdministracionOperativaModule { }
